@import url('https://fonts.googleapis.com/css2?family=Sora:wght@300&display=swap');
* {
  box-sizing: border-box !important;
}
body {
  background-color: #f5faff;
}
.title {
  font-weight: 800;
  margin-bottom: 0;
  padding-bottom: 0;
  margin-top: 20px;
}
.line {
  background-color: rgb(160, 49, 49);
  width: 120px;
  height: 4px;
  margin: auto;
  margin-top: 0;
}
.button-wrapper {
  margin-top: 20px;
  padding-left: 20px;
}
.button-wrapper .btn {
  color: rgb(160, 49, 49);
  font-weight: 600;
  margin: 12px;
  text-transform: capitalize;
}
.button-wrapper .btn:hover {
  border: 2px solid blue;
  background-color: rgb(160, 49, 49);
  color: #f5faff;
}
.button-wrapper .btn:active {
  border: 2px solid rgb(160, 49, 49);
}
.wrapper {
  margin-top: 25px;
  display: flex;
  /* color: #fff; */
  width: 100%;
}
.each-menu {
  /* align-self: flex-start; */
  display: block;
  height: 100%;
  width: 100%;
}
.img-box {
  max-width: 200px;
  max-height: 150px;
  margin-right: 20px;
  border-radius: 5px;
  border: 2px solid rgb(160, 49, 49);
  box-shadow: 1px 2px black;
}
.image {
  height: 100%;
  width: 100%;
}
.details {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  font-weight: 700;
  margin-bottom: 0;
  text-transform: capitalize;
}
.title2 {
  font-size: 20px;
}
.price {
  justify-self: flex-end;
  margin-left: 0;
  color: rgb(160, 49, 49);
  font-size: 20px;
}
.demacate {
  width: 100%;
  background-color: lightgrey;
  height: 2px;
}
.info {
  font-weight: 600;
}
@media only screen and (max-width: 768px) {
  .wrapper {
    margin-top: 25px;
    display: block;
    /* color: #fff; */
    width: 100%;
  }
  .img-box {
    max-width: 90vw;
    max-height: 200px;
    margin: auto;
    /* margin-right: 20px; */
    border-radius: 5px;
    border: 2px solid rgb(160, 49, 49);
    box-shadow: 1px 2px black;
    margin-bottom: 20px;
  }
  .image {
    max-height: 200px;
    max-width: 100%;
  }
  .button-wrapper {
    display: inline-block;
    margin-top: 20px;
    /* padding-top: 5px; */
    /* padding-left: 10px; */
  }
  .button-wrapper .btn {
    color: rgb(160, 49, 49);
    font-weight: 600;
    margin: 5px;
    text-transform: capitalize;
  }
}
